import React, {Component} from "react"
import MyPhoto from "../Images/Md. Sakib Khan.jpg"

import {FaGithub, FaLinkedin} from "react-icons/fa"

class LeftView extends Component{
    render(){
        return(

                <div className = "grid-container ">
                <div class="row navbar navbar-dark bg-light mt-2">
                <div className = "col-sm-2 ">
                <button className = "btn btn-lg text-dark p-3" onClick = {this.props.Changed} value = "About">About</button>
                </div>
                <div className = "col-sm-2   ">
                <button className = "btn btn-lg  text-dark p-3 " onClick = {this.props.Changed} value = "Education">Education</button>
                </div>
                <div className = "col-sm-2   ">
                <button className = "btn btn-lg text-dark p-3 " onClick = {this.props.Changed} value = "Thesis">Research Projects</button>
                </div>
                <div className = "col-sm-2   ">
                <button className = "btn btn-lg text-dark p-3" onClick = {this.props.Changed} value = "Projects">Software Projects</button>
                </div>
                <div className = "col-sm-2   ">
                <button className = "btn btn-lg text-dark p-3 " onClick = {this.props.Changed} value = "WorkHistory">Work History</button>
                </div>
                <div className = "col-sm-2   ">
                <button className = "btn btn-lg text-dark p-3 "  onClick = {this.props.Changed} value = "Skills">Skills</button>
                </div>
                </div>
                </div>

        );
    }
}

export default LeftView;